import Vue from "vue";
import Vuex from "vuex";
const axios = require("axios");
const SET_USERNAME = "setUsername";
const SET_SESSION_VALIDATION = "setSessionValidation";
const SESSION_VALIDATED = "sessionValidated";
const VALIDATE_SESSION = "validateSession";
const jsSha = require("js-sha256");
Vue.use(Vuex);
let getCookieList = () => {
  var cookieList = document.cookie.split(";");
  var dictionary = {};
  for (var item in cookieList) {
    var elements = item.split("=");
    dictionary[elements[0]] = elements[1];
  }
  return dictionary;
};
export default new Vuex.Store({
  state: {
    userID: "",
    sessionID: "",
    username: "",
    passHash: "",
    debugText: "",
    sessionValidated: false
  },
  getters: {
    sessionID: state => {
      let dictionary = getCookieList();
      state.sessionID = dictionary["sid"];
      return state.sessionID;
    },
    userID: state => {
      let dictionary = getCookieList();
      state.userID = dictionary["uaid"];
    },
    sessionValidated: state => {
      return state.sessionValidated;
    }
  },
  mutations: {
    setUsername: (state, username) => {
      state.username = username;
    },
    setPass: (state, password) => {
      state.passHash = jsSha.sha256(password);
    },
    setDebugText: (state, text) => {
      state.debugText = text;
    }
  },
  actions: {
    createUser: ({ commit, state }) => {
      return new Promise((resolve, reject) => {
        axios({
          method: "post",
          url: "/api/user",
          data: {
            username: state.username,
            passHash: state.passHash
          }
        })
          .then(() => {
            resolve(state.username);
          })
          .catch(err => {
            reject(err);
          });
      });
    },
    fetchUsername: ({ commit, state }) => {
      return new Promise((resolve, reject) => {
        axios({
          method: "post",
          url: "/api/user/fetchByName",
          data: {
            value: state.userID
          }
        }).then(res => {
          commit({
            type: "setUsername",
            username: res.data.username
          });
          resolve();
        });
      });
    },
    startSession: ({ commit, state }) => {
      return new Promise((resolve, reject) => {
        axios({
          method: "post",
          url: "/api/validation/startSession",
          data: {
            username: state.username,
            passHash: state.passHash
          }
        })
          .then(res => {
            state.dispatch(VALIDATE_SESSION).then(() => {
              resolve(state.sessionID);
            });
          })
          .catch(err => {
            if (err.status === 404) {
              commit({
                type: "setDebugText",
                text: state.username + "/" + state.passHash
              });
              axios({
                method: "post",
                url: "/api/user",
                data: {
                  username: state.username,
                  password: state.passHash
                }
              });
            }
          });
      });
    }
  },
  validateSession: ({ commit, state }) => {
    return new Promise((resolve, reject) => {
      axios({
        method: "post",
        url: "/api/validation/validateSession",
        data: {
          value: state.username
        }
      }).then(res => {
        commit({
          type: SET_SESSION_VALIDATION,
          valid: res.data.validated
        });
        commit({
          type: "setDebugText",
          text: res.data + "/" + state.sessionValidated
        });
        resolve();
      }); //probably catch errors and send to server
    });
  }
});
