<template lang="pug">
mixin dropdownMenu(title, ...items)
    div(class="dropdown")
        button(type="button", class="btn btn-outline-light", data-toggle = "dropdown")
            | #{title}
        div(class="dropdown-menu")
            each item in items
                a(class="dropdown-item" href="#") #{item}

    
div(id="nav")
    router-link(to="#")
        img(src="../assets/logo-msa.png")
        | |
    div(class="navy")
        h3(text-variant="light")
        //- +dropdownMenu("About", "Our Mission", "Our Team", "Our Trainers", "Giving Back")
        //- +dropdownMenu("Training", "Classes", "Webinars", "Members&Certification", "Training Schedule")
        //- +dropdownMenu("Product Resources","Tech Tips", "Submit a Tech Tip")
        //- +dropdownMenu("News")
        //- +dropdownMenu("Events")
        //- +dropdownMenu("Contact")
        //- div(v-if="sessionValidated===true")
            
        //- div(v-else)
        b-form(inline)
            b-form-input(v-model="username", placeholder="Username")
            
            b-form-input(v-model="password", placeholder="Password")
        
        b-btn(variant="primary") Primary
        b-btn(variant="secondary") Secondary
        b-btn(variant="info") Info

                
</template>

<script>
import {mapState, mapGetters, mapActions, mapMutations} from 'vuex';
export default {
}
</script>


<style lang="scss" scoped>
$msa-off-white: rgb(242, 242, 242);
$msa-navy-blue: rgb(27, 43, 59);
.dropdown {
    display: inline-block;
}
.navy {
    background-color:$msa-navy-blue;
}
#nav {
    background-color: $msa-off-white;
}
</style>